.madeonline-header {
  position: sticky;
  top: 0px;
  background-color: rgba(255, 255, 255, 0.9294117647);
  z-index: 99;
  opacity: 0.9;
}
.madeonline-header .main-menu-sidebar {
  display: none;
}
@media only screen and (max-width: 991.98px) {
  .madeonline-header .main-menu-sidebar {
    display: block;
    height: 100%;
  }
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .logo {
  display: flex;
  align-items: center;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .logo img {
  height: 60px;
  width: auto;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper {
  background-color: #fff;
  position: absolute;
  right: 0;
  left: 0;
  top: 77px;
  display: none;
  justify-content: center;
  align-items: center;
  transition: ease-in-out 0.3s;
  transition: 0.4s;
  -webkit-box-shadow: 0px 6px 13px -3px rgb(120, 120, 120);
  -moz-box-shadow: 0px 6px 13px -3px rgb(120, 120, 120);
  box-shadow: 0px 6px 13px -3px rgb(120, 120, 120);
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links {
  transition: 0.4s;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 9999;
  text-align: center;
  padding: 30px 0;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item {
  padding: 10px;
  border-bottom: 1px solid #efefef;
  text-align: start;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item a {
  padding: 10px 0;
  font-size: 18px;
  font-weight: 500;
  color: #2b59c3;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item a:hover {
  color: #f36c2a;
  transition: ease-in-out 0.3s;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item-has-children {
  position: relative;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item-has-children .made-chevron {
  position: absolute;
  top: 0px;
  right: 15px;
  padding: 10px;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item-has-children .dropdown-some {
  display: block;
  background-color: #f7d6c6;
  margin-top: 15px;
  display: none;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item-has-children .dropdown-some.hide-nav .menu-item {
  transition: ease-in-out 0.3s;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item-has-children .dropdown-some.hide-nav .menu-item a {
  font-size: 16px;
  transition: ease-in-out 0.3s;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap #sidebar-menu-wrapper .nav-links .menu-item-has-children .dropdown-some.hide-nav .menu-item a:hover {
  color: #f36c2a;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu {
  display: flex;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu .bar {
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 25px;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu .bar .bar-1 {
  width: 5em;
  display: block;
  width: 40px;
  padding-top: 5px;
  margin-top: 4px;
  cursor: pointer;
  transition: 0.3s;
  background-color: #f36c2a;
  border-radius: 20px;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu .bar .bar-2 {
  width: 5em;
  display: block;
  width: 40px;
  padding-top: 5px;
  margin-top: 4px;
  display: fixed;
  transition: 0.3s;
  background-color: #f36c2a;
  border-radius: 20px;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu .bar .bar-3 {
  width: 5em;
  display: block;
  width: 40px;
  padding-top: 5px;
  margin-top: 4px;
  transition: 0.3s;
  background-color: #f36c2a;
  border-radius: 20px;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu .change .bar-1 {
  -webkit-transform: rotate(-45deg) translate(-10px, 5px);
  transform: rotate(-45deg) translate(-7px, 8px);
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu .change .bar-2 {
  opacity: 0;
}
.madeonline-header .main-menu-sidebar .sidebar-menu-wrap .burger-menu .change .bar-3 {
  -webkit-transform: rotate(45deg) translate(-10px, 5px);
  transform: rotate(45deg) translate(-5px, -6px);
}
.madeonline-header .desktop-menu {
  z-index: 9;
  height: 100px;
  display: flex;
